import React from 'react';
import PropTypes from 'prop-types';
import { Icon } from '@iconify/react';
import CheckmarkFill from '@iconify/icons-eva/checkmark-fill';
import { withStyles } from '@material-ui/styles';
import { Box, Step, StepConnector, StepLabel, Stepper } from '@material-ui/core';

const QontoConnector = withStyles((theme) => ({
  alternativeLabel: {
    top: 10,
    left: 'calc(-50% + 20px)',
    right: 'calc(50% + 20px)',
  },
  active: {
    '& $line': { borderColor: theme.palette.primary.main },
  },
  completed: {
    '& $line': { borderColor: theme.palette.primary.main },
  },
  line: {
    borderTopWidth: 2,
    borderColor: theme.palette.divider,
  },
}))(StepConnector);

function QontoStepIcon({ active, completed }) {
  return (
    <Box
      sx={{
        zIndex: 9,
        width: 24,
        height: 24,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        color: active ? 'primary.main' : 'divider',
        bgcolor: 'background.default',
      }}
    >
      {completed ? (
        <Box component={Icon} icon={CheckmarkFill} sx={{ zIndex: 1, width: 20, height: 20, color: 'primary.main' }} />
      ) : (
        <Box
          sx={{
            width: 8,
            height: 8,
            borderRadius: '50%',
            backgroundColor: 'currentColor',
          }}
        />
      )}
    </Box>
  );
}

QontoStepIcon.propTypes = {
  active: PropTypes.bool,
  completed: PropTypes.bool,
};

const Steps = ({ steps, activeStep }) => (
  <Stepper alternativeLabel activeStep={activeStep} connector={<QontoConnector />}>
    {steps.map((label) => (
      <Step key={label}>
        <StepLabel
          StepIconComponent={QontoStepIcon}
          sx={{
            '& .MuiStepLabel-label': {
              typography: 'subtitle2',
              color: 'text.disabled',
            },
          }}
        >
          {label}
        </StepLabel>
      </Step>
    ))}
  </Stepper>
);

Steps.propTypes = {
  steps: PropTypes.array,
  activeStep: PropTypes.number,
};

export default Steps;
